import { Component, OnInit, ViewChild } from '@angular/core';
import { JwtService } from '../../../../shared-ui/jwt.service';
import { currentUser } from '../../../../layouts/home-layout/user.model';
import * as moment from 'moment';
import { ActivatedRoute } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { GlobalService } from '../../../../shared-ui/global.service';
import { Company } from '../../../../shared-ui/models/global.model';
import { ToastrService } from 'ngx-toastr';
import { ModalDirective } from 'ngx-bootstrap';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

  @ViewChild("addGroupModal", { static: false })
  public addGroupModal: ModalDirective;

  currentUser: currentUser = new currentUser;
  selectedGroups: any = [];
  addedGroup: String = "";

  constructor(
    private jwtService: JwtService,
    private route: ActivatedRoute,
    private spinner: NgxSpinnerService,
    private globalService: GlobalService,
    private toastr: ToastrService,
  ) {
    this.currentUser = JSON.parse(this.jwtService.getCurrentUser());
    this.route.params.subscribe((res) => {
      if(res.id){
        this.getCompany(res.id);
      }
    });

  }

  company: Company = new Company;

  ngOnInit() {
  }

  getCompany(id: any){
    this.spinner.show();
    this.globalService.getCompany({ id: id}).subscribe(data => {
      if(Object.keys(data).length){
        data.Items.forEach(element => {
          if(element['id'] == id){
            this.company = element;
          }
        });
      }
      this.spinner.hide();
    }, error => {
      this.toastr.error('There are some server error! Please check connection.', 'Error');
    });
  }

  saveCompany(){
    let postData = {
      name: this.company.name,
      isActive: this.company.isActive ? 1 : 0,
      creator: this.currentUser.id  
    }
    this.globalService.saveCompany(postData).subscribe(data => {

    });
  }

  updateCompany(){
    let postData = {
      name: this.company.name,
      isActive: this.company.isActive ? 1 : 0,
      modifier: this.currentUser.id  
    }
    this.globalService.updateCompany(postData, this.company['id']).subscribe(data => {

    });
  }

  selectGroup(group){
    let found = this.selectedGroups.filter(e => e == group);
    if(found.length){
      this.selectedGroups.splice(this.selectedGroups.indexOf(found[0]), 1);
    }else{
      this.selectedGroups.push(group);
    }
  }

  showAddGroupModal(){
    this.addedGroup = "";
    this.addGroupModal.show();
  }
  
  addGroup(){
    this.company.groups.push(this.addedGroup);
    this.addGroupModal.hide();
  }

  deleteGroup() {
    if(confirm("Are you sure to delete selected groups")) {
      this.selectedGroups.forEach(element => {
        this.company.groups.splice(this.company.groups.indexOf(element), 1);
        this.selectedGroups.splice(this.selectedGroups.indexOf(element), 1);
      });
    }
  }

}
